import React,{useState} from 'react';
import MaterialTable from "material-table";
import {createMuiTheme, makeStyles} from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import TextField from '@material-ui/core/TextField';
import Divider from '@material-ui/core/Divider'
import FilledInput from '@material-ui/core/FilledInput';
import Form from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import {costRender,costCount,mergeCount} from './calculate';

import skill_table from './excel/skill_table';
import {data} from './chatacterData';


import AddBox from '@material-ui/icons/AddBox';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';

const tableIcons = {
    Add: AddBox,
    Check: Check,
    Clear: Clear,
    Delete: DeleteOutline,
    DetailPanel: ChevronRight,
    Edit: Edit,
    Export: SaveAlt,
    Filter: FilterList,
    FirstPage: FirstPage,
    LastPage: LastPage,
    NextPage: ChevronRight,
    PreviousPage: ChevronLeft,
    ResetSearch: Clear,
    Search: Search,
    SortArrow: ArrowUpward,
    ThirdStateCheck: Remove,
    ViewColumn: ViewColumn
};


const arkTheme = createMuiTheme({
    palette: {
        primary: {
            light: '#434343',
            main: '#1c1c1c',
            dark: '#000000',
            contrastText: '#fff',
        },
        secondary: {
            light: '#434343',
            main: '#1c1c1c',
            dark: '#000000',
            contrastText: '#fff',
        },
    },
});

const useStyles = makeStyles(theme => ({
    root:{
        display:'flex',
        flexDirection:'column',
    },
    navigateWrap:{
        margin:'10px 0',
        display: 'flex',
        justifyContent:'space-between',
        width:'100%',
        '&>div':{
            display:'flex',
        },
        '&>div>div':{
            display:'flex',
            border:'1px solid #000',
            borderRadius:'2px',
            padding:'1px 3px',
            margin:'0 5px',
        },
        userSelect:'none',
    },
    container: {
        display: 'flex',
        alignItems:'baseline',
    },
    tableContentMasterSkill:{
        display: 'flex',
        flexDirection:'column',
        alignItems:'flex-star',
        '&>div':{
            display: 'flex',
            justifyContent: 'space-between',
            padding:'0 5px 0 0'
        }
    },
    material:{
        width:'100%',
        minWidth:'594px',
        filter: 'drop-shadow(0px 4px 4px rgba(7, 7, 7, 0.8))',
        background: '#404040',
        color:'white',
        padding:'7.5px 10px',
        '& div': {
            flexWrap: 'wrap',
            display: 'flex',
        },
        '& div>div':{
            whiteSpace:'nowrap',
            margin:'2.5px 5px',
        }
    },
    middleForm: {
        display: 'flex',
        flexDirection:'column',
        alignItems: 'flex-end',
        width: 'fit-content',
    },
    nameWrap:{
        width:'90%',
        margin:'auto',
    },
    long:{
        width:'100%',
    },
    short:{
        width:'100px',
    },
    formControl: {
        margin: '8px',
        // width:'120px',
    },
}));

const boundary=[[0,2],[1,7],[0,3]];
const arrayIsValid= e =>(e[0]||e[0]===0)&&(e[1]||e[0]===0)&&e[0]!==''&&e[1]!=='' ;

function valueToList(id,value) {
    if(!(id in data)){
        return false;
    }
    const func=e=> arrayIsValid(e)?e:[];
    let temp = [
        id,
        func([value.upgrade.from, value.upgrade.to]),
        func([value.allSkill.from, value.allSkill.to]),
                    data[id]?[
                        data[id].skills[0]?func([value.masterSkill[0].from, value.masterSkill[0].to]):[],
                        data[id].skills[1]?func([value.masterSkill[1].from, value.masterSkill[1].to]):[],
                        data[id].skills[2]?func([value.masterSkill[2].from, value.masterSkill[2].to]):[],
                    ]:[],
    ]
    // console.log(temp);
    for(let i of [1,2]){
        if(temp[i].length>0) {
            if (temp[i][0] > temp[i][1])
                return false;
            for (let j of [0, 1]) {
                if ((!(temp[i][j] >= boundary[i - 1][0])) || (!(temp[i][j] <= boundary[i - 1][1])))
                    return false
            }
        }
    }
    if (temp[3].length>0){
        for(let i of temp[3]){
            if(i[0]>i[1])
                return false;
            for(let j of i){
                if( (!(j>=boundary[2][0])) || (!(j<=boundary[2][1])) )
                    return false
            }
        }
    }
    return temp
}

function parserValue(list) {
    return ({
        name:data[list[0]].name,
        upgrade:{
            from:list[1][0],
            to:list[1][1],
        },
        allSkill:{
            from:list[2][0],
            to:list[2][1],
        },
        masterSkill:{
            0: {
                from: list[3][0][0],
                to: list[3][0][1],
            },
            1: {
                from: list[3][1][0],
                to: list[3][1][1],
            },
            2: {
                from: list[3][2][0],
                to: list[3][2][1],
            },
        }
    });
}

const example={
    name:'Amiya',
    upgrade:{
        from:'0',
        to:'2',
    },
    allSkill:{
        from:1,
        to:7,
    },
    masterSkill: {
        0: {
            from: 0,
            to: 3,
        },
        1: {
            from: 0,
            to: 3,
        },
        2: {
            from: 0,
            to: 3,
        },
    },
}
const example_list=valueToList('char_002_amiya',example);

const rankNameList=['','1','2'];
const rank_array_to_string = e=> arrayIsValid(e)&&rankNameList[e[0]]&&rankNameList[e[1]] ? `${rankNameList[e[0]]}  ${rankNameList[e[1]]}`:'';
const array_to_string = e=> arrayIsValid(e)?`${e[0]}  ${e[1]}`:'';
function Main(props) {
    const classes = useStyles();
    const headerStyle={
        textAlign:'left',
        padding:'5px',
        width:'min-content',
        minWidth:'100px',
        maxWidth:'220px',
    };
    const [dataTable, setDataTable] = React.useState({
        columns: [
            { title: '', field: 'id',sorting:true,editable:'never', cellStyle:headerStyle,
                render: rowData => <span>{data[rowData.id].name}</span>},
            { title: '', field: 'upgrade' ,sorting:false, cellStyle:headerStyle,
                render: rowData => <span>{rank_array_to_string(rowData.upgrade)}</span>},
            { title: ' ', field: 'allSkill', type: 'numeric' ,sorting:false, cellStyle:headerStyle,
                render: rowData => <span>{array_to_string(rowData.allSkill)}</span>},
            {title: ' ', field: 'masterSkill', type: 'numeric',sorting:false, cellStyle:headerStyle,
                render: rowData => <div className={classes.tableContentMasterSkill}>{rowData.masterSkill.map((x,index)=>(
                    (x.length>0&&array_to_string(x)!==''&&
                        <div>
                            <div>{skill_table[data[rowData.id].skills[index]].levels[0].name}</div>
                            <div>{array_to_string(x)}</div>
                        </div>
                    )
                ))}</div>},
        ],
        data: [{
            id:example_list[0],
            upgrade:example_list[1],
            allSkill:example_list[2],
            masterSkill:example_list[3],
        }],
    });
    const [myState,setMyState]=useState('');
    const [chosenId,setId]=useState(null);
    const [value,setValue]=useState({
        name:'',
        upgrade:{
            from:'',
            to:'',
        },
        allSkill:{
            from:'',
            to:'',
        },
        masterSkill:{
            0: {
                from: '',
                to: '',
            },
            1: {
                from: '',
                to: '',
            },
            2: {
                from: '',
                to: '',
            },
        },

    });
    const [helpText,setHelpText]=useState({
        name:'e.g. Amiya',
        upgrade:{
            from:'',
            to:'',
        },
        allSkill:{
            from:'',
            to:'',
        },
        masterSkill:{
            0: {
                from: '',
                to: '',
            },
            1: {
                from: '',
                to: '',
            },
            2: {
                from: '',
                to: '',
            },
        },
    });

    function nameHandle(event) {
        if (event.target.value.trim().length===0){
            setId(null);
            setValue({...value,name:''});
            setHelpText({...helpText,name:''});
        }else if(event.target.value.trim() in data) {
            let temp = event.target.value.trim();
            setHelpText({...helpText,
                name:data[temp].name + '/'+ data[temp].appellation+'/'+data[temp].id});
            setId(temp);
            setValue({...value,name:data[temp].name});
            return
        }else {
            let re = new RegExp('^' + event.target.value.trim(), 'i');
            let filtered = Object.keys(data).filter(function (key) {
                if (re.test(data[key].id)) {
                    return true
                }
                if (re.test(data[key].appellation)) {
                    return true
                }
                if (re.test(data[key].name)) {
                    return true
                }
            });
            if (filtered.length === 1){
                setId(filtered[0]);
                setHelpText({...helpText,
                    name:data[filtered[0]].name+'/'+data[filtered[0]].appellation+'/'+data[filtered[0]].id });
            };
        }
        setValue({...value, name:event.target.value});
    };
    function upgradeHandle(event,key,key_1) {
        let output='';
        if (event.target.value.trim() === ''){
            output='';
        }else if (0<=event.target.value && 2>=event.target.value){
            output=rankNameList[event.target.value];
        }else{
            output='Invalid.';
        }
        setHelpText({...helpText,[key]:{...helpText[key],[key_1]:output}});
        setValue({...value, [key]:{...value[key],[key_1]:event.target.value}});
    }
    function allSkillHandle(event,key,key_1) {
        let output='';
        if (event.target.value.trim() === ''){
            output='';
        }else if (1<=event.target.value && 7>=event.target.value){
            output='';
        }else{
            output='Invalid.';
        }
        setHelpText({...helpText,[key]:{...helpText[key],[key_1]:output}});
        setValue({...value, [key]:{...value[key],[key_1]:event.target.value}});
    }
    function masterSkillHandle(event,index,key) {
        setValue({...value, masterSkill:{...value.masterSkill,[index]:{...value.masterSkill[index],[key]:event.target.value}}});
    }
    function exampleHandle() {
        nameHandle({target:{value:'Amiya'}});
        setValue(example);
    }

    function pushHandle() {
        let raw = valueToList(chosenId,value);
        if (raw) {
            let temp=dataTable.data;
            temp.push({id:raw[0],upgrade:raw[1],allSkill:raw[2],masterSkill:raw[3]})
            setDataTable({...dataTable,data:temp});
            setMyState('Push success.');
        } else {
            setMyState('Invalid');
        }

        // console.log(value);
        // console.log(raw);
        // console.log(cost);
        // console.log(costRender(cost));
    };


    const materialDict=dataTable.data.length>0?mergeCount(dataTable.data.map(x=>costCount(x.id, x.upgrade, x.allSkill, x.masterSkill))):null;

    return (
        <ThemeProvider theme={arkTheme}>
            <MaterialTable
                icons={tableIcons}
                title=""
                options={{
                    headerStyle:headerStyle,
                    search: false,
                }}
                columns={dataTable.columns}
                data={dataTable.data}
                localization={{header:{actions:'',},}}
                actions={[
                    {
                        icon: DeleteOutline,
                        tooltip: '',
                        onClick: (event, rowData) => {
                                        setTimeout(() => {
                                            const temp = [...dataTable.data];
                                            temp.splice(temp.indexOf(rowData), 1);
                                            setDataTable({ ...dataTable, data:temp });
                                        }, 600);
                    }}
                ]}
            />
            {dataTable.data.length>0&&[
                <br/>,
                <div className={classes.material}>
                    
                    <div>
                    {costRender(materialDict)}
                    </div>
                </div>,]
            }
            <br/>
            <Divider />
            <div className={classes.root}>
                <div className={classes.navigateWrap}>
                    <div>
                        <div onClick={exampleHandle}>Example</div>
                    </div>
                    <div>
                        <div onClick={pushHandle}>Push</div>
                    </div>
                </div>

                <div className={classes.container}>
                    {myState}
                </div>

                <div className={classes.middleForm}>
                    <div className={classes.nameWrap}>
                        <TextField
                            className={[classes.formControl,classes.long]}
                            label="Name/ID"
                            type="text"
                            value={value.name}
                            onChange={nameHandle}
                            helperText={helpText.name}
                            margin="normal"
                        />
                    </div>
                    <div className={classes.container}>
                    <div>
                        
                    </div>
                    <TextField
                        className={[classes.formControl,classes.short]}
                        label="From"
                        type="number"
                        value={value.upgrade.from}
                        onChange={(e)=>upgradeHandle(e,'upgrade','from')}
                        helperText={helpText.upgrade.from}
                        margin="normal"
                    />
                    <TextField
                        className={[classes.formControl,classes.short]}
                        label="To"
                        type="number"
                        value={value.upgrade.to}
                        onChange={(e)=>upgradeHandle(e,'upgrade','to')}
                        helperText={helpText.upgrade.to}
                        margin="normal"
                    />
                </div>
                <div className={classes.container}>
                    <div>
                         
                    </div>
                    <TextField
                        className={[classes.formControl,classes.short]}
                        label="From"
                        type="number"
                        value={value.allSkill.from}
                        onChange={(e)=>allSkillHandle(e,'allSkill','from')}
                        helperText={helpText.allSkill.from}
                        margin="normal"
                    />
                    <TextField
                        className={[classes.formControl,classes.short]}
                        label="To"
                        type="number"
                        value={value.allSkill.to}
                        onChange={(e)=>allSkillHandle(e,'allSkill','to')}
                        helperText={helpText.allSkill.to}
                        margin="normal"
                    />
                </div>
                    {(chosenId in data)&&(data[chosenId].skills.length>0)&&(
                        data[chosenId].skills.map((id,index)=>(data[chosenId].masterSkillCost[index].length>0&&(
                                    <div className={classes.container}>
                                        <div>
                                            {skill_table[id].levels[0].name+' '}
                                        </div>
                                        <TextField
                                            className={[classes.formControl,classes.short]}
                                            label='From'
                                            type="number"
                                            value={value.masterSkill[index].from}
                                            onChange={(e)=>masterSkillHandle(e,index,'from')}
                                            // helperText={'From'}
                                            margin="normal"
                                        />
                                        <TextField
                                            className={[classes.formControl,classes.short]}
                                            label='To'
                                            type="number"
                                            value={value.masterSkill[index].to}
                                            onChange={(e)=>masterSkillHandle(e,index,'to')}
                                            // helperText={'To'}
                                            margin="normal"
                                        />
                                    </div>
                                )
                            )
                        )
                    )
                    }
                </div>
            </div>
        </ThemeProvider>
    );
}

export default Main;